<script lang="ts">
import EditBudget from "./components/edit/EditBudget.svelte";
import ViewBudget from "./components/read/ViewBudget.svelte";
import NewBudget from "./components/create/NewBudget.svelte";
import Navigator from "./components/Navigator.svelte";
import NotLoggedIn from "./components/misc/NotLoggedIn.svelte";
import Login from "./components/misc/Login.svelte";
import { isLoggedIn } from "./auth";
import router from "page";

let page = null;

router("/", () => {
	page = ViewBudget;
});

router("/new", () => {
	page = NewBudget;
});

router("/edit", () => {
	page = EditBudget;
});

router("/login", () => {
	page = Login;
});

router("/logout", () => {
	page = Login;
});

router.start();

let isLoggedInPromise = isLoggedIn();
</script>

{#await isLoggedInPromise}
	<p>...</p>
{:then loggedIn}
	Loggedin = {loggedIn}
	<Navigator {loggedIn} />
	{#if loggedIn}
		<svelte:component this={page} />
	{:else}
		<NotLoggedIn />
	{/if}
{:catch error}
	<p>{error.message}</p>
{/await}

<style>
:global(body) {
	/* Not sure if materialize or svelte is adding a padding, but something is */
	padding: 0px !important;
}
</style>
